import React, { Component } from 'react';
import axios from 'axios'
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import ReactTags from 'react-tag-autocomplete'
import { HashLink as Link } from 'react-router-hash-link';
import './styles.css'
import Card from 'react-bootstrap/Card';

//This Component is a child Component of Customers Component
export default class TaggedItems extends Component {

  constructor(props) {
    super(props);
    this.state = {
      tags: [
        { id: "#1:0", name: "Program-French" },
        { id: "#2:0", name: "ArSP" },
      ],

    }
  }

  getTagListData() {
    axios.get('assets/tags/taglist.json').then(response => {
      this.setState({ tagList: response.data })
      this.setState({ suggestions: response.data })
    })
  };

  //function which is called the first time the component loads
  componentDidMount() {
    this.getTagListData();
  }

  //Function which is called whenver the component is updated
  componentDidUpdate(prevProps) {

    //get Customer Details only if props has changed
    if (this.props.val !== prevProps.val) {
      this.getTagDetails(this.props.val)
    }
  }

  onDelete(i) {
    const tags = this.state.tags.slice(0)
    tags.splice(i, 1)
    this.setState({ tags })
    let selectedTag = this.state.tagList.filter(e => { if (e.id === this.props.currentId) return e })[0];

  }

  onAddition(tag) {
    const tags = [].concat(this.state.tags, tag)
    this.setState({ tags })
  }

  //Function to Load the customerdetails data from json.
  // getTagDetails(id) {
  //   axios.get('assets/tags/tag' + id + '.json').then(response => {
  //     this.setState({ customerDetails: response })
  //   })
  // };

  handleInputChange = (img) => {

  }
  render() {
    if (!this.state.tagList) return (<p>Loading Data</p>);
    let selectedTag = this.state.tagList.filter(e => { if (e.id === this.props.currentId) return e })[0];
    return (<div id="editTags" className="customerdetails">
      <Card bsStyle="info" className="centeralign">
        <Card.Header>
          <h3><center><b>Edit Tags</b></center></h3>
          <Card.Title componentClass="h3"><p><b>Tag Name:</b> <div className="badge primary">{selectedTag.name}&nbsp;</div>  <b>Description:</b>{selectedTag.description}</p></Card.Title>
          <b>ParentTags:</b> {selectedTag.parenTagstHirearchy.map(tag => {
            return (tag === "" ? "" : <div className="badge primary">{tag}&nbsp;</div>);
          })}
          <p>
            <b>ChildTags:</b> {selectedTag.childTagsHirearchy.map(tag => {
              return (tag === "" ? "" : <div className="badge primary">{tag}&nbsp;</div>);
            })}
          </p>
          <center>This Tag is taggged to below images, each image will have their own tag list, which can be added or removed.</center>

          {/* <Link to={{ pathname: "/taglist#preview", id: this.props.currentId }} className="btn btn-primary">Preview</Link> */}
        </Card.Header>
        <Card.Body>
          <br />
          <Carousel dynamicHeight={true}>
            {selectedTag.tagged.map(img => {
              return (<div>
                <img src={'./images/' + img.src} />
                <p><b> File:{img.src},{img.title}</b></p>
                <form style={{ 'textAlign': 'left' }}>
                  <label>Title: </label><input class="form-control" name="title"
                    type="text"
                    value={img.title}
                    onChange={this.handleInputChange} />
                  <label>Description: </label><input class="form-control" name="description"
                    type="text"
                    value={img.description}
                    onChange={this.handleInputChange} />
                </form>
              </div>);
            })}
          </Carousel>
          <ReactTags
            ref={this.reactTags}
            tags={this.state.tags}
            autoresize={false}
            suggestions={this.state.suggestions}
            onDelete={this.onDelete.bind(this)}
            onAddition={this.onAddition.bind(this)}
            minQueryLength={1}
            classNames={{
              root: 'react-tags',
              rootFocused: 'is-focused',
              selected: 'react-tags__selected',
              selectedTag: 'react-tags__selected-tag',
              selectedTagName: 'react-tags__selected-tag-name',
              search: 'react-tags__search',
              searchWrapper: 'react-tags__search-wrapper',
              searchInput: 'react-tags__search-input',
              suggestions: 'react-tags__suggestions',
              suggestionActive: 'is-active',
              suggestionDisabled: 'is-disabled'
            }}
          />
        </Card.Body>
      </Card>
    </div >)
  }
}